<template>
	<transition name="detail">
		<div class="detail-wrapper" ref="detailScroll" v-show="detailIsShow">
			<div class="movie-detail" v-if="detailData">
				<div class="content-wrapper">
					<div class="content" >
						<div class="img-wrapper">
							<img :src="detailData.images.large" width="50%">
						</div>
						<div class="introduce">
							<div class="aka common">
								<span>别名：</span>
								<span v-for="aka in detailData.aka">{{aka}}<span>、</span></span>
							</div>
							<div class="cast common">
								<span>演员：</span>
								<span v-for="cast in detailData.casts">{{cast.name}}<span>、</span></span>
							</div>
							<div class="country common">
								<span>国家：</span>
								<span v-for="country in detailData.countries">{{country}}<span>、</span></span>
							</div>
							<div class="director common">
								<span>导演：</span>
								<span v-for="director in detailData.directors">{{director.name}}<span>、</span></span>
							</div>
							<div class="genre common">
								<span>类型：</span>
								<span v-for="genre in detailData.genres">{{genre}}<span>、</span></span>
							</div>
							<div class="rating">
								<span>评分：</span>
								<span >{{detailData.rating.average}}</span>
							</div>
							<div class="year">
								<span>年代：</span>
								<span >{{detailData.year}}</span>
							</div>
						</div>
						<div class="desc">
							<div class="summary">影片简介：</div>
							<span class="text">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{detailData.summary}}</span>
						</div>
					</div>
				</div>
				<div class="close"><span class="iconfont icon-guanbi" @click="hideDetail"></span></div>
			</div>
			<div class="loading" v-else><span>正在加载……</span></div>
		</div>
	</transition>
</template>

<script>
	// import BScroll from 'better-scroll'
	export default {
		data() {
			return {
				detailIsShow: false,
				detailData: ''
			}
		},
		methods: {
			requestDetail(id) {
				// console.log(id)
				this.detailData = ''
				this.$http.jsonp(`https://api.douban.com/v2/movie/subject/${id}`)
				.then((data) => {
					if (data.ok) {
						// console.log(data.body)
						this.detailData = data.body
					}
				})
			},
			showDetail() {
				this.detailIsShow = true
			},
			hideDetail() {
				this.detailIsShow = false
			}
		}
	}
</script>

<style lang="less" rel="stylesheet/less">
	.detail-wrapper{
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: 150;
		background-color: rgba(0, 0, 0, .9);
		// overflow: hidden;
		transition: all 0.3s;
		&.detail-enter,&.detail-leave-active{
			transform: translate3d(100%, 0, 0);
		}
		.movie-detail{
			width: 100%;
			height: 100%;
			overflow: auto;
			.content-wrapper{
				width: 100%;
				height: auto;
				min-height: 100%;
				.content{
					padding: 20px 20px 80px 20px;
					color: #fff;
					.img-wrapper{
						text-align: center;
						margin-bottom: 20px;
					}
					.introduce{
						font-size: 14px;
						.common{
							span:last-child{
								span{
									display: none;
								}
							}
						}
						div{
							margin-top: 10px;
						}
					}
					.desc{
						margin-top: 10px;
						font-size: 14px;
						line-height: 16px;
						.summary{

						}
						.text{

						}
					}
				}
			}
			.close{
				position: relative;
				z-index: 10;
				width: 100%;
				height: 60px;
				margin-top: -60px;
				text-align: center;
				clear: both;
				span{
					font-size: 30px;
					color: #fff;
				}
			}
		}
		.loading{
			// position: relative;
			height: 100%;
			width: 100%;
			text-align: center;
			span{
				display: inline-block;
				color: #fff;
				margin-top: 50%;
			}
		}
	}
		
</style>



